.Tools {
    background-color: #f9fbfd;
    padding-bottom: 80px;
    padding-top: 40px;
    position: relative;
}
.Tools, .Tools:before {
    background-position: bottom right;
    background-repeat: no-repeat;
}

.Tools-title {
    font-family: Poppins,sans-serif;
    margin: 0 0 12px;
    font-size: 48px;
    font-weight: bold;
    color: #324168;
}

.Tools-description {
    font-size: 18px;
    margin: 0 0 24px;
}

.Tools-lists {
    display: flex;
}

.Footer-copyright, .Nav-link, .Tools-description {
    color: #707f8f;
}

.Tools-list {
    display: flex;
    flex-direction: column;
}

.u-reset-ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Footer a, .Nav a, .Tools a {
    text-decoration: none;
}

.Tools-link {
    color: #707f8f;
    font-size: 16px;
    line-height: 34px;
    transition: ease-in-out 200ms color;
}

.Tools-link:hover, .Tools-showMore:hover {
    color: rgb(94, 85, 173);
}

.Footer a, .Nav a, .Tools a {
    text-decoration: none;
}

.Tools-img {
    width: 365px;
    margin-left: 90px;
}

@media (min-width: 800px){
    .Tools {
        padding-bottom: 123px;
        padding-top: 80px;
    }
}

@media (min-width: 800px){
    .Tools-title {
        font-size: 32px;
        line-height: 24px;
        margin-bottom: 8px;
    }
}

@media (min-width: 800px){
    .Tools-description {
        margin-bottom: 40px;
    }
}

@media (min-width: 800px){
    .Tools-lists {
        display: flex;
    }
}

@media (min-width: 800px){
    .Tools-list {
        display: flex;
        flex-direction: column;
    }
}

@media (min-width: 800px){
    .Tools-list {
        margin-right: 96px;
    }
}

@media (min-width: 800px){
    .Tools-item {
        max-width: 266px;
    }
}

@media (min-width: 800px){
    .Tools-link {
        font-size: 16px;
        line-height: 34px;
    }
}
