section.DiscoverWays {
    height: 750px;
}

.discoverWaysImage {
    float: left;
    width: 40%;
    padding-left: 65px;
    padding-top: 100px;
}

.weProvide {
    margin-top: 15px;
    float: right;
    width: 48%;
}

.weProvideLi {
    width: 504px;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.08);
    background-color: #ffffff;
    list-style: none;
    display: flex;
    margin-bottom: 38px;
}

.weProvideLi div{
    margin-top: 42px;
}

.weProvide-img {
    margin: 50px 35px;
}

.head-details-provide{
    text-align: left;
    color: #324168;
    font-family: Inter,sans-serif;
    font-size: 24px;
    font-weight: 500;
}

.sub-details-provide{
    text-align: left;
    color: #324168;
    font-family: Inter,sans-serif;
    width: 356px;
    height: 41px;
    font-size: 16px;
}

.weProvideLi:hover{
    margin-left: 50px;
    transition: ease-in-out 200ms all;
    background-color: #ececec;
}