.slick-prev {
    left: 54% !important;
    z-index: 1;
    height: 0px !important;
}

.slick-slider {
    margin-top: 43px !important;
    display: flow-root !important;
    width: 1342px !important;
}

.slick-next {
    right: 1% !important;
    height: 0px !important;
    z-index: 1;
}

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
    color: transparent !important;
    outline: none !important;
}

.slick-prev, .slick-next {
    position: absolute !important;
}

.slick-next:before, .slick-prev:before {
    font-size: 20px;
    line-height: 1;
    opacity: .75;
    color: black;
}

.slick-list {
    margin: 0;
    float: right !important;
    width: 47% !important;
    height: 364px !important;
}

.slick-dots {
    width: 51% !important;
    height: 396px !important;
    display: flex !important;
    flex-wrap: wrap;
    bottom: unset !important;
    margin: 0px 0px 0px 35px !important;
}

.slick-dots li {
    display: flex !important;
    width: 326px !important;
    height: 89px !important;
}

.box-image {
    width: 65px;
    height: 65px;
    border-radius: 20px;
    background-color: #b4cbe4;
}

.li-fit-content:hover .box-image{
    transition: ease-in 200ms;
    background-color: #324168;
}

.li-fit-content:hover .box-image .img-centre{
    color: snow;
}

.li-fit-content {
    width: 346px !important;
    height: 90px !important;
    position: unset !important;
    padding: 12px 0px 0px 12px !important;
    cursor: pointer;
    margin-bottom: 12px;
    flex-wrap: wrap;
    display: inline-table;
}

.img-centre {
    margin: 16px;
}

.li-fit-left { 
    float:left; 
    width: 24%; 
    height: 66px; 
} 
.li-fit-right{ 
    padding-top: 8px;
    float:left;  
    width:72%; 
    height: 66px; 
    text-align: left;
}

.main-li-heading{
    font-family: Poppins, sans-serif;
    font-size: 20px;
    font-weight: bold;
    color: #394d84;
}

.li-description{
    font-family: Poppins, sans-serif;
    display: flex;
    font-size: small;
}

.slick-active .li-fit-content:hover{
    transition: ease-in-out 200ms all;
}
 
.slick-active {
    background: #ebeeee;
    transition: ease-in-out 200ms all;
    border-radius: 6px;
    font-weight: 600;
    margin-right: 20px;
}

.slick-active .li-fit-content .box-image{
    transition: ease-in 200ms;
    background-color: #324168;
}

.slick-active .li-fit-content .box-image .img-centre{
    color: snow;
}